import React from 'react';

import Error from './Error';

import { colors, fonts } from '../constants';

import ChevronArrowIcon from './icons/ChevronArrow';

export default function Select({ error, label, ...props }) {
  const { options } = props;

  return (
    <div>
      {label && <label className="label">{label}</label>}
      <div className={`input ${error && 'input_invalid'}`}>
        <select className="input__line" {...props}>
          <option value="">Please select</option>
          {options.map(item => {
            if (typeof item.value === 'undefined') {
              return (
                <option key={item} value={item}>
                  {item}
                </option>
              );
            }
            return (
              <option key={item.value} value={item.value}>
                {item.label}
              </option>
            );
          })}
        </select>
        <div className="icon">
          <ChevronArrowIcon />
        </div>
      </div>
      {error && <Error error={error} />}
      <style jsx>{`
        .label {
          font-size: 16px;
          font-weight: 500;
          line-height: 2;
          letter-spacing: 0.4px;
          color: ${colors.label};
        }
        .input {
          border-radius: 3px;
          border: solid 1px ${colors.inputBorder};
          margin-bottom: 20px;
          display: flex;
          position: relative;
        }
        .input_invalid {
          border: solid 1px ${colors.inputError};
          margin-bottom: 5px;
        }
        .input__line {
          padding: 12px;
          border: none;
          font-family: ${fonts.halyardDisplay};
          font-size: 18px;
          line-height: 1.33;
          letter-spacing: 0.4px;
          color: ${colors.input};
        }
        .input__line:focus,
        .input__line:active {
          outline: none;
        }
        .input__line::placeholder {
          font-family: ${fonts.halyardDisplay};
          font-size: 18px;
          line-height: 1.33;
          letter-spacing: 0.4px;
          color: ${colors.inputPlaceholder};
        }
        select {
          align-items: center;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          background-color: #fff;
          border: 1px solid #d4d6d7;
          border-radius: 8px;
          cursor: pointer;
          display: block;
          font-family: inherit;
          font-size: 1rem;
          outline: 0;
          padding: 0 1rem;
          height: 45px;
          resize: none;
          width: 100%;
        }
        .input .icon {
          position: absolute;
          top: 10px;
          right: 10px;
          pointer-events: none;
        }
      `}</style>
    </div>
  );
}
